import React, { useCallback, useMemo } from "react";
import {
  SafeAreaView,
  View,
  StyleSheet,
  Button,
  Pressable,
  TouchableOpacity,
  Text,
  Alert,
  Image
} from "react-native";
import { ScrollView } from "react-native-gesture-handler";
import colors from "./styles/colors";
import profile_pic from '../assets/profile_pic.jpg';
import Swiper from 'react-native-swiper/src';

{/** Sticking with a solid theme set here, so I'll use FA! */}
import { MaterialCommunityIcons, MaterialIcons } from 'react-native-vector-icons';

import {PressableOpacity} from 'react-native';


export function LandingPageScreen({ navigation }) {

  return (
    <SafeAreaView style={styles.screen}> 
            <Swiper
                horizontal={true}
                showsButtons={true}
                automaticallyAdjustContentInsets={true}
                bounces={true}
                activeDot={
                    <View
                      style={{
                        backgroundColor: '#fff',
                        width: 13,
                        height: 13,
                        borderRadius: 7,
                        marginLeft: 7,
                        marginRight: 7
                      }}
                    />
                }
                paginationStyle={{
                    bottom: 25
                }}
                nextButton={
                        <Text style={{color:colors.white, textAlign:'center', justifyContent:'center'}}><MaterialIcons name="arrow-forward-ios" size={25}></MaterialIcons></Text>
                    
                }
                prevButton={
                    <Text style={{color:colors.white, textAlign:'center', justifyContent:'center'}}><MaterialIcons name="arrow-back-ios" size={25}></MaterialIcons></Text>
                }
            >
                <View style={styles.slide1}>
                    <View style={styles.imageWrapper}>
                        <View style={styles.profilePictureContainer}>
                            <Image
                                source={profile_pic}
                                style={styles.profilePicture} 
                            />
                        </View>
                        <View>
                            <Text style={styles.text}>Hello, World!</Text>
                        </View>
                        <View style={styles.nameRow}>
                            <View style={styles.nameInner}>
                                <MaterialCommunityIcons name="badge-account-horizontal-outline" style={{color:colors.black, paddingTop:4}} size={25}/>
                                <View style={{paddingLeft: 10}}>
                                    <Text style={styles.text}>
                                        Jon Perry
                                    </Text>
                                </View>
                                
                            </View>
                            <View>

                            </View>
                        </View>
                        <View style={styles.jobTitleRow}>
                            <View style={styles.jobInnerRow}>
                                <MaterialCommunityIcons name="remote-desktop" style={{color: colors.black, paddingTop:4}} size={25}/>
                                <View style={{paddingLeft: 10}}>
                                    <Text style={styles.text}>
                                        Machine Learning Engineer
                                    </Text>   
                                </View>
                                
                            </View>
                            <View>

                            </View>
                        </View>
                    </View>
                </View>
                <View style={styles.slide2}>
                <Text style={styles.text}>Stuff</Text>
                </View>
                <View style={styles.slide3}>
                <Text style={styles.text}>Some more stuff</Text>
                </View>
            </Swiper>
    </SafeAreaView>
  )
}
const styles = StyleSheet.create({
    screen:{
        flex:1,
        backgroundColor: colors.offwhite,
    },
    text:{
        color: colors.black,
        paddingVertical: 10,
    },
    swiperwrapper: {
    },
    socialBar: {
        backgroundColor: colors.gray,
        flexDirection:'row',
        justifyContent:'center',
    },
    itemOutterContainer: {
        paddingVertical: 20,
        width:'90%',
        justifyContent:'center',
        margin:'auto',
    },
    itemInnerContainer:{
        backgroundColor: 'transparent',
        padding:10,
        borderRadius:30,
        justifyContent: 'center'
    },
    itemWrapper:{
        paddingHorizontal: 20,
        paddingVertical: 50,
        justifyContent: 'center',
        width:'100%',
        margin:'auto',
    },
    itemTitle: {
        fontSize: 34,
        fontWeight:'700',
        color: colors.black,
        fontFamily: 'Roboto',
        paddingBottom: 15,
        textAlign: 'center',
    },
    profilePicture:{
        width:180,
        height: 180,
        borderRadius:90,
        borderColor:colors.grayopacity,
        borderWidth: 2,
    },
    imageWrapper:{
        flex:1,
        flexDirection:'column',
        justifyContent:'center',
        alignItems: 'center',
    },
    nameRow:{
        width:'100%',
        flexDirection:'row',
        justifyContent:'center'
    },
    jobTitleRow:{
        width:'100%',
        flexDirection:'row',
        justifyContent:'center'
    },
    jobInnerRow:{
        flexDirection:'row',
    },
    nameInner:{
        flexDirection:'row',
        paddingTop: 20,
        paddingHorizontal: 15,


    },
    nameText:{
        fontFamily:'Roboto',
        fontSize:24,
        fontWeight:'600',
        paddingLeft: 15,
    },
    slide1: {
        flex:1,
        flexDirection:'row',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: 'rgba(165,178,215,1)'
    },
    slide2: {
    flex:2,
    flexDirection:'row',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'rgba(210,217,235,1)'
    },
    slide3: {
    flex:1,
    flexDirection:'row',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'rgba(165,178,215,0.8)'
    },
    text: {
    color: colors.black,
    fontSize: 24,
    fontWeight: 'bold'
    },
    profilePicture:{
        width:180,
        height: 180,
        borderRadius:90,
        borderColor:colors.grayopacity,
        borderWidth: 2,
    },
    profilePictureContainer:{
        paddingBottom:20,
    },
    nameRow:{
        width:'100%',
        flexDirection:'row',
        justifyContent:'center'
    },
    jobTitleRow:{
        width:'100%',
        flexDirection:'row',
        justifyContent:'center'
    },
    jobInnerRow:{
        flexDirection:'row',
    },
    nameInner:{
        flexDirection:'row',
        paddingTop: 20,
        paddingHorizontal: 15,


    },
});